body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-sf-pro-display: "SF Pro Display";

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-29xl: 48px;
  --font-size-lg: 18px;
  --font-size-13xl: 32px;
  --font-size-xs: 12px;
  --font-size-25xl: 44px;
  --font-size-21xl: 40px;

  /* Colors */
  --color-whitesmoke-100: #f7f7f8;
  --color-whitesmoke-200: #e9eaec;
  --color-gray-100: #070708;
  --color-gray-200: rgba(7, 7, 8, 0.78);
  --color-white: #fff;
  --color-dimgray: #535455;
  --color-gold: #ffef5c;
  --color-darkslategray: #393a3c;
  --color-lavender: #e3dffb;

  /* Gaps */
  --gap-xl: 20px;
  --gap-9xs: 4px;
  --gap-xs: 12px;
  --gap-base: 16px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-10xs: 3px;
  --padding-xs: 12px;
  --padding-9xs: 4px;
  --padding-5xl: 24px;
  --padding-xl: 20px;
  --padding-smi-9: 12.9px;
  --padding-lg: 18px;
  --padding-29xl: 48px;
  --padding-88xl: 107px;
  --padding-lgi: 19px;
  --padding-xs-3: 11.3px;
  --padding-base-4: 16.4px;
  --padding-base-3: 16.3px;
  --padding-17xl: 36px;
  --padding-41xl: 60px;
  --padding-smi: 13px;

  /* border radiuses */
  --br-80xl: 99px;
  --br-21xl: 40px;
  --br-xs: 12px;
  --br-5xl: 24px;
  --br-7xs: 6px;
  --br-5xs: 8px;
  --br-45xl: 64px;
  --br-xl: 20px;
}
